import { createWebHistory, createRouter } from "vue-router";
import G_HomeWrapper from '@/components/G_HomeWrapper.vue'
import G_About from '@/components/G_About.vue'
import G_CardWrapper from '@/components/G_CardWrapper.vue'
import G_CardContentWrapper from '@/components/G_CardContentWrapper.vue'
import G_MemberWrapper from '@/components/G_MemberWrapper.vue'
import G_MemberInfo from '@/components/G_MemberInfo.vue'



const routes = [
  {
    path: "/",
    component: G_HomeWrapper,
    props: true
  },
  {
    path: "/space",
    component: G_CardWrapper,
    props: true
  },
  {
    path: "/about",
    component: G_About,
    props: true
  },
  {
    path: "/channel/:channel_id",
    component: G_CardWrapper,
    props: true
  },
  {
    path: "/space/:pid?/:dim1/:dim2",
    component: G_CardWrapper,
    props: true
  },
  {
    path: "/space/content/:contentId?",
    component: G_CardContentWrapper,
    props: true
  },
  {
    path: "/member",
    component: G_MemberWrapper,
    props: true
  },
  {
    path: "/author/:id",
    component: G_MemberInfo,
    props: true
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});


router.beforeEach((to, from, next) => {
  //console.log("router")
  //ga.logEvent('goal_completion1', { name: 'lever_puzzle'});
  setTimeout(function(){
    
    //ga.logEvent('goal_completion2', { name: 'lever_puzzle'});
    
    ga.setCurrentScreen(window.location.pathname) // sets `screen_name` parameter
    ga.logEvent('screen_view') // log event with `screen_name` parameter attached
  },10)
  
  next()
})
export default router;